#tools {
    flex: 1;
    width: 100%;
    flex-direction: row;
    
    
    .app__wrapper {
        padding: 0;
        .copyright {
            display: none;
        }
    }
}
.ball{
    width: 10px;
    height: 10px;
    background-color: black;
    border-radius: 50%;
}
.app__skills{
    display: flex;
    flex-direction: column;
}

.app__skills-container {
    width: 100%;
    margin-top: 3rem;

    display: flex;
    flex-direction: row;

    @media (max-width:900px) {
        width: 100%;
        flex-direction: column;
    }


}

.app__skills-list {
    flex: 1;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: flex-start;

    margin-right: 5rem;

    @media (max-width:900px) {
        margin-right: 0;
        justify-content: center;
        align-items: center;
    }

}

.app__skill-item {
    flex-direction: column;
    text-align: center;

    margin: 1rem;

    transition: all 0.3s ease-in-out;

    div {
        width: 90px;
        height: 90px;

        border-radius: 50%;

        background-color: #fff;

        img {
            width: 50%;
            height: 50%;
        }

        &:hover {
            box-shadow: 0 0 25px #fef4f5;
        }

        @media (min-width:2000px) {

            width: 150px;
            height: 150px;
        }

        @media (max-width:900px) {
            width: 70px;
            height: 70px;
        }
    }

    p{
        font-weight: 500;
        margin-top: 0.5rem;
    }

    @media (min-width:2000px) {
    margin: 1rem 2rem;
    
    p{
        margin-top: 1rem;
        
    }
    
    }
}

.app__skills-exp{
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;

    padding-left: 2rem;

    @media (max-width:900px) {
        margin-top: 2rem;

    }

    @media (max-width:450px) {
        padding-left: 0;
        justify-content: center;
        align-items: center;
    }
}



.app__skills-exp-work{
    flex: 1;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;

    cursor: pointer;
    margin-bottom: 1rem;


    width: 80%;

    h4{
        font-weight: 500;
    }

    p{
        font-weight: 400;
        color: var(--gray-color);

        margin-left: 1rem;

    }



}

#titel{
    max-width: 300px;
    box-shadow: 0 0 25px rgba(0,0,0,0.1);
    background-color: var(--white-color);
    border-radius: 5px;
    padding: 1rem;
    color: var(--gray-color);
    text-align: center;
    line-height: 1.5;
    opacity: 1;

    @media (min-width:2000px) {
        font-size: 1.75rem;
        max-width: 500px;
        line-height: 2;        
    }

}