.app__about{
    flex-direction: column;
    
    

}
#about{
.app__wrapper {
    padding: 0;

    .copyright {
        display: none;
    }
}
}

.app__profiles{
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-wrap: wrap;

    .app__what-ican-do{
        width: 190px;
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;
        flex-direction: column;
        margin: 2rem;
        h2{
            margin: 1rem 0;
        }
        img{
            height: 170px;
            width: 100%;
            object-fit: cover;
            border-radius: 15px;
        }

        @media (min-width:2000px) {
            width: 370px;
            margin: 2rem 4rem;

            img{
                height: 320px;
            }
        }
    }
}